    position: relative;
    background-color: #0052CC;
    color: #FAFBFC;
}

.package-button:hover {
    color: #FAFBFC;
    background-color: #0065ff;
}

.package-button .tooltiptext {
    visibility: hidden;
    width: 100px;
    background-color: white;
    color: #444444;
    text-align: center;
    border: 1px solid #2998F9;
    border-radius: 8px;
    padding: 2px 0;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 17px;
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    bottom: 120%;
    left: 50%;
    cursor: pointer;
    margin-left: -60px;
}

.package-button .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 10px;
    border-style: solid;

    border-color: #2998F9 transparent transparent transparent;
}

.package-button:hover .tooltiptext {
    visibility: visible;
}

