    }
}

@media all and (min-width:482px) and (max-width:600px) {
    .export-project-list {
        display: block !important;
    }
}

@media all and (min-width:601px) and (max-width:992px) {
    .export-project-list {
        float: right;
    }
}

@media all and (min-width:481px) and (max-width:992px) {
    .pm-name-edit-part {
        display: flex;
    }

    .desktop-width {
        min-width: 250px;
        max-width: 300px;
    }

    .fix-action-btn-alignment {
        display: block;

    }
}

@media all and (min-width:992px) {
    .ongoing-action-card-buttons {
        text-align: right;

    }

    .fix-action-btn-alignment {
        float: right !important;
    }

    .export-project-list {
        float: right;
    }
}

@media all and (min-width:1179px) {
    .pm-name-edit-part {
        display: flex;
    }

    .confirm-name-pm, .cancel-name-pm {
        margin-top: -3px
    }

    .desktop-width {
